.movie {
  background-color: rgba(239, 243, 247, 0.9);
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  text-decoration: none;
  color: inherit;
  box-shadow: 0 13px 27px -5px rgba(231, 231, 234, 0.768),
    0 8px 16px -8px rgba(231, 231, 234, 0.668),
    0 -6px 16px -6px rgba(231, 231, 234, 0.368);
}

.movie__img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year {
  margin: 0;
  font-weight: 600;
  text-decoration: none;
}

.movie__title a {
  margin-bottom: 5px;
  font-size: 20px;
  color: #2c2c2c;
  text-decoration: none;
}
.summary {
  opacity: 0.6;
  font-weight: 400;
}
.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
  opacity: 0.5;
}

.movie__genres li,
.movie__year {
  margin-right: 10px;
  font-size: 14px;
}
